import { FC } from 'react'
import Slider from 'react-slick'
import styles from './HeaderCarousel.module.scss'

type HeaderSliderT = {
  images: { image: string }[]
}

export const HeaderSlider: FC<HeaderSliderT> = ({ images }) => {
  const settings = {
    infinite: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 2000,
    pauseOnHover: true,
  }

  return (
    <div className={styles.wrapper} id={'header-slider'}>
      {images.length > 1 ? (
        <Slider {...settings}>
          {images.map(({ image }, index) => (
            <img src={image} key={index} loading="lazy" />
          ))}
        </Slider>
      ) : (
        <img className={styles.soloImage} src={images[0].image} loading="lazy" />
      )}
    </div>
  )
}
